@import '../../helpers/_variables.scss';

.nav-pills .uib-tab { /*bootstrap*/
    display: -webkit-box;
    display: -moz-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-align-items: center;
    align-items: center;
    -moz-align-items: center;
    -webkit-align-items: center;
    justify-content: center;
} 

.form-control, .btn { 
  font-size: $font-normal; 
}

.form-control {
    line-height: 1.5;
    border-radius: 2rem;
    padding: .75rem 1.125rem;
    height: auto;
    &[readonly] {
        background-color: $grey-lighter;
        border: 0;

    }
    &:focus {
        box-shadow: 0 0 0 0 rgba(0, 0, 0, 0)
    }
}

select.form-control {
    &:not([size]):not([multiple]) {
        height: inherit;
    }
}

.btn {
    line-height: $line-none;
}

.nav-link {
    padding: 0;
}

.btn {
    padding-left: 0;
    padding-bottom: 0;
    padding-right: 0;
    padding-top: 0;
    border-radius: 0;
}

.table tr:first-child {
    > td {
        border-top: 0;
    }
} 

uib-progress.progressblue { height: 3rem; }

.table thead th { border-bottom: 1px solid #dee2e6!important; }

.tab-content {
    width: 100%;
}

.nav-tabs {
    border: none!important;
}

.modal-open {
    overflow: scroll;
}

.navbar {
    padding: 0!important;
}



.nav-tabs .nav-link.active, 
.nav-tabs .nav-link { border: none; }

.justify-content-between ul.nav-pills, 
.justify-content-between .notification-pills { 
    -webkit-justify-content: space-between;
    -moz-justify-content: space-between;
    justify-content: space-between; /*for uib elements*/ 
}

.btn-block+.btn-block { margin-top: 0px; }

.carousel-inner {
    max-height: 49px;
    overflow: hidden;
}

.next-caret, .prev-caret {
    z-index: 999; 
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-pack: center;
    justify-content: center;
    width: 80px;
    color: #fff;
    text-align: center;
    opacity: .5;
    &:hover {
        text-decoration: none;
        opacity: 1;
    }
}

.modal-content {
    border: none!important;
    width: auto;
}


.modal.in .modal-dialog {
    -webkit-transform: translate(0, 0);
    -o-transform: translate(0, 0);
    transform: translate(0, 0);
}

.wide-modal {
    .modal-dialog {
        max-width: 80rem;
    }
}

.modal-dialog {
    height: 100%;
    width: auto;
    max-width: 65rem;
    display: -webkit-box;
    display: -moz-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-align-items: center;
    align-items: center;
    -webkit-justify-content: center;
    -moz-justify-content: space-between;    
    justify-content: center;
    min-height: 100%;  
}

.modal-content {
    width: 100%;
}

.offset-xl-5 {
    margin-left: 41.666667%;
}

.form-check-input, .form-check-inline .form-check-input {
    margin-left: 1rem;
}

.form-check-label {
    margin-left: 1rem;
    margin-top: 1px;
}

.popover {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1038;
    display: none;
    max-width: 276px;
    padding: 1px;
    font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.42857143;
    text-align: left;
    text-align: start;
    text-decoration: none;
    text-shadow: none;
    text-transform: none;
    letter-spacing: normal;
    word-break: normal;
    word-spacing: normal;
    word-wrap: normal;
    white-space: normal;
    background-color: #fff;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
    border: 1px solid #ccc;
    border: 1px solid rgba(0,0,0,.2);
    border-radius: 6px;
    -webkit-box-shadow: 0 5px 10px rgba(0,0,0,.2);
    box-shadow: 0 5px 10px rgba(0,0,0,.2);
    line-break: auto;
}

.popover-content {
    padding: 9px 14px;
}

[uib-popover-popup].popover.top-right > .arrow {
    top: auto;
    bottom: auto;
    left: auto;
    right: auto;
    margin: 0;  
}

.popover>.arrow:after {
    content: "";
    border-width: 10px;

}

// .popover>.arrow, .popover>.arrow:after {
//     position: absolute;
//     display: block;
//     width: 0;
//     height: 0;
//     border-color: transparent;
//     border-style: solid;
// }

.popover.left>.arrow {
    top: 50%;
    right: -11px;
    margin-top: -11px;
    border-right-width: 0;
    border-left-color: #999;
    border-left-color: rgba(0,0,0,.25);
    &:after {
        right: 1px;
        content: " ";
        border-right-width: 0;
        border-left-color: #fff;
    }
}


.popover.right>.arrow {
    top: 50%;
    left: -11px;
    margin-top: -11px;
    border-right-color: #999;
    border-right-color: rgba(0,0,0,.25);
    border-left-width: 0;
    &:after {
      left: 1px;
      content: " ";
      border-right-color: #fff;
      border-left-width: 0;
    }
}

.popover.bottom>.arrow {
    top: -11px;
    left: 50%;
    margin-left: -11px;
    border-top-width: 0;
    border-bottom-color: #999;
    border-bottom-color: rgba(0,0,0,.25);
    &:after {
        top: 1px;
        content: " ";
        border-top-width: 0;
        border-bottom-color: #fff;
    }
}


.popover.top>.arrow {
    bottom: -11px;
    left: 50%;
    margin-left: -11px;
    border-top-color: #999;
    border-top-color: rgba(0,0,0,.25);
    border-bottom-width: 0;
    &:after {
        bottom: 1px;
        content: " ";
        border-top-color: #fff;
        border-bottom-width: 0;
    } 
}

.progress {
    height: 1.75rem;
}

.progress-bar-danger {
    background: #d9534f
}

.progress-bar-warning {
    background-color: #f0ad4e;
}

.progress-bar-success {
    background-color: #5cb85c;
}

.nav-link {
    &.disabled {
        pointer-events: auto;
        cursor: not-allowed;
    }
 }

.align-items-stretch {
    align-items: stretch;
}