@import '../helpers/_variables.scss';

.steps {
    min-height: 90px;
    padding: 30px 0 0 0;
    font-family: 'Open Sans', sans-serif;
    position: relative;
    .steps-container {
        background: #DDD;
        height: 6px;
        width: 100%;
        border-radius: 10px;
        -moz-border-radius: 10px;
        -webkit-border-radius: 10px;
        -ms-border-radius: 10px;
        margin: 0;
        list-style: none;
        padding-left: 0;
        li {
            text-align: left;
            list-style: none;
            float: left
        }       
    } 
}


.steps .steps-container li.active .step .step-image, 
.steps .steps-container li.complete .step .step-image, 
.failed .steps .steps-container li.active .step .step-image, 
.failed .steps .steps-container li.complete .step .step-image {
    margin: -12px 0 0 0
}

.steps .steps-container li:last-child .step .step-image {
    margin: -1.5px 0 0 0;
    display: flex;
    -moz-justify-content: center;
    -webkit-justify-content: center;
    justify-content: center;

}

.success .steps .steps-container li.active:last-child .step .step-image, .failed .steps .steps-container li.active:last-child .step .step-image, .success .steps .steps-container li.complete:last-child .step .step-image, .failed .steps .steps-container li.complete:last-child .step .step-image {
    margin: -12px 0 0 0;
    display: flex;
    justify-content: flex-end;
}



.success .steps .steps-container {
    background: $green;
}

.failed .steps .steps-container {
    background: $red;
}

.success .steps  .steps-container li .step .step-image span {
    background: $green;
}

.failed .steps  .steps-container li .step .step-image span {
    background: $red;
}

.success .steps .steps-container li.complete .step .step-image span,  .success .steps  .steps-container li.active .step .step-image span, .failed .steps .steps-container li.complete .step .step-image span,  .failed .steps  .steps-container li.active .step .step-image span {
/*    background-color: #DDD;*/
    display: flex;
    width: 57px;
    height: 30px;
    border-radius: 15px;
    -moz-border-radius: 15px;
    -webkit-border-radius: 15px;
    -ms-border-radius: 15px
}

.steps .steps-container li .step .step-image span {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #DDD;
    display: block;
    width: 10px;
    height: 10px;
    margin: 0 auto;
    border-radius: 15px;
    -moz-border-radius: 15px;
    -webkit-border-radius: 15px;
    -ms-border-radius: 15px    
}

.steps .steps-container li .step .step-current {
    font-size: 11px;
    font-style: italic;
    color: #999;
    margin: 8px 0 0 0
}

.success .steps .steps-container li.complete .step .step-image span:after, 
.failed .steps .steps-container li.complete .step .step-image span:after {
    position: absolute;
    z-index: 1;
    width: 47px;
    height: 20px;
    margin: 5px;
    border-radius: 15px;
    -moz-border-radius: 15px;
    -webkit-border-radius: 15px;
    -ms-border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: center;    
}

.success .steps .steps-container li.complete .step .step-image span, .success .steps .steps-container li.active .step .step-image span {
    background-color: $green; /*green*/
}

.failed .steps .steps-container li.complete .step .step-image span, .failed .steps .steps-container li.active .step .step-image span {
    background-color: $red;/*red*/
}

.failed .steps .steps-container li.complete .step .step-image span:after, .success .steps .steps-container li.complete .step .step-image span:after, .steps .steps-container li.failed .step .step-image span:after {
    display: flex;
    -moz-justify-content: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;      
    justify-content: center;
    align-items: center;
display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.4rem;

}

.success .steps .steps-container li.active .step .step-image span:after {
    display: -webkit-box; 
    display: -moz-box;
